header {
	position: fixed;
	display: flex;
	flex-wrap: wrap;
	width: 100%;
	top: 0;
	padding: 30px 0px;
	z-index: 500;
	align-items: center;
	transition: .2s all ease-in-out;

	&.active {
		background: #1a1d1e;
	}

	.left-menu {
		padding-right: 15px;
		padding-left: 15px;
		display: flex;
		flex: 0 0 33.333%;

		.logo {
			img {
				width: 35px;
			}
		}
	}

	.add-event {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);

		a {
			display: flex;
			align-items: center;

			svg {
				width: 24px;
				margin-right: 10px;

				path,
				circle {
					stroke: $main-color-1;
				}
			}
		}

		span {
			font-family: 'Anton', sans-serif;
			color: $main-color-3;
			font-size: 20px;
			letter-spacing: 2px;
		}
	}

	.right-menu {
		margin-left: auto;
		display: flex;
		align-items: center;

		.button-login {
			padding-right: 50px;
			padding-left: 15px;

			a {
				display: flex;
				align-items: center;

				svg {
					margin-right: 10px;

					g {
						transition: .3s all ease-in-out;
					}
				}

				span {
					font-family: 'Anton', sans-serif;
					color: $main-color-3;
					font-size: 20px;
					letter-spacing: 2px;
				}
			}

			&:hover {
				svg {
					g {
						stroke: $main-color-1;
					}
				}
			}
		}

		.avatar {
			position: relative;
			margin-right: 50px;

			figure {
				cursor: pointer;
				user-select: none;
				display: flex;
				align-items: center;

				.box-img {
					position: relative;
					overflow: hidden;
					width: 28px;
					height: 28px;
					border-radius: 50%;
					margin-right: 10px;

					&:after {
						content: '';
						position: absolute;
						width: 100%;
						height: 100%;
						top: 0;
						left: 0;
						border: 2px solid $main-color-1;
						border-radius: 50%;
					}
				}

				figcaption {
					h5 {
						font-family: 'Anton', sans-serif;
						font-weight: 400;
						color: $main-color-3;
						font-size: 20px;
						letter-spacing: 2px;
					}
				}
			}

			#sub-information {
				position: fixed;
				border: 1px solid #555555;
				background: $main-color-2;
				width: 40%;
				height: 100vh;
				top: 0;
				right: -100%;
				transition: 1s all ease-in-out;
				padding: 50px 0;

				&.active {
					right: 0;
				}

				.close {
					cursor: pointer;
					position: absolute;
					top: 30px;
					right: 30px;
					z-index: 10;

					svg {
						path {
							transition: .3s all ease-in-out;
						}
					}

					&:hover {
						svg {
							path {
								fill: $main-color-1;
								transition: .3s all ease-in-out;
							}
						}
					}
				}

				figure {
					display: flex;
					align-items: center;
					padding: 0 50px;

					.box-img {
						position: relative;
						overflow: hidden;
						width: 100px;
						height: 100px;
						border-radius: 50%;
						margin-right: 20px;

						&:after {
							content: '';
							position: absolute;
							width: 100%;
							height: 100%;
							top: 0;
							left: 0;
							border: 2px solid $main-color-1;
							border-radius: 50%;
						}
					}

					figcaption {
						h5 {
							font-family: 'Anton', sans-serif;
							font-weight: 400;
							color: $main-color-3;
							font-size: 20px;
							letter-spacing: 2px;

							span {
								margin-left: 30px;
								color: $main-color-1;
							}
						}

						a {
							color: $main-color-3;
						}
					}
				}

				.list-item {
					margin-top: 50px;

					.item {
						color: $main-color-3;
						border-bottom: 1px solid rgba(70, 70, 70, 0.44);
						margin-bottom: 15px;
						padding: 0 50px;
						line-height: 50px;
						transition: .3s all ease-in-out;

						&:hover {
							color: $main-color-1;
						}
					}
				}
			}
		}

		.button-search {
			cursor: pointer;
		}
	}
}
