.clients-partner {
	position: relative;
	background-image: url(../img/clients-partner/1.jpg);
	background-position: center center;
	padding: 50px 0;
	height: 500px;

	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: #000000dc;
		z-index: 0;
		pointer-events: none;
	}

	.left-item {
		position: relative;
		display: flex;
		align-items: center;
		z-index: 1;
		height: 370px;

		figure {
			text-align: center;

			figcaption {
				justify-content: center;

				h3 {
					margin: 10px 0;
				}

				p {
					text-align: center;
				}
			}

			.btn {
				font-size: 10px;
				width: 80px;
				line-height: 30px;
				display: block;
				margin: 20px auto 0;
				letter-spacing: 1px;
				border-radius: 5px;

				&:hover {
					color: $main-color-2;
				}
			}
		}
	}

	.right-item {
		position: relative;
		z-index: 1;

		.tab-container {
			padding: 0 50px;
			margin-top: 20px;

			.tab-content {
				margin-top: 50px;
			}
		}

		.list-tab {
			display: flex;
			justify-content: space-between;

			.item {
				cursor: pointer;
				user-select: none;
				color: $main-color-1;
				padding: 10px;
				border: 1px solid $main-color-1;
				transition: .3s all ease;

				&:hover {
					color: #000000;
					background: $main-color-1;
				}

				&.active {
					color: #000000;
					background: $main-color-1;
				}
			}
		}

		.list-img {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			.item {
				width: 20%;
				margin-bottom: 30px;
			}
		}
	}
}
